/* eslint-disable import/no-relative-packages */
/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './editor.scss';
import '../../../shared/variables.css';
import { Provider } from 'react-redux';
import { SnackbarProvider } from 'notistack';
import { ConfigProvider } from 'antd';
import { useBlockProps } from '@wordpress/block-editor';
import { useEffect } from '@wordpress/element';
import { theme } from '../../../theme.config';
import { configureAppStore } from '../../../shared/store/configureStore';
import { i18nInit } from '../../../shared/i18n/i18n';
import Container from './Container';
import { getCurrentLocale } from '../../../shared/API/getTraduireSansMigraineVariables';
import handleButton from './handleButton';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @return {Element} Element to render.
 */
const store = configureAppStore();
export default function Edit(args) {
  const { context } = args;

  i18nInit(getCurrentLocale());

  const getProductId = () => {
    const url = new URL(window.location.href);
    const path = url.searchParams.get('path') || '';
    return path.split('/').pop() || '';
  };

  useEffect(() => {
    handleButton();
  }, []);

  return (
    <ConfigProvider theme={theme}>
      <Provider store={store}>
        <SnackbarProvider
          maxSnack={5}
          anchorOrigin={{
            horizontal: 'left',
            vertical: 'top',
          }}
        >
          <Container
            productId={getProductId()}
            attributes={useBlockProps()}
            context={context}
          />
        </SnackbarProvider>
      </Provider>
    </ConfigProvider>
  );
}
