import axiosClient from '../axiosClient';
import { WordpressAnswer } from '../typesResponse';
import { Settings } from '../../types/Settings';

type SuccessResponse = Record<string, never>;
export const updateSettings = async ({
  body,
}: {
  body: {
    settings: Partial<Record<keyof Settings, boolean>>;
  };
}): Promise<WordpressAnswer<SuccessResponse>> => {
  const action = 'traduire-sans-migraine_update_settings';
  const response = await axiosClient.post<SuccessResponse>('', body, {
    params: { action },
  });

  return response.data;
};
