import axiosClient from '../axiosClient';
import { WordpressAnswer } from '../typesResponse';
import { ActionType } from '../../types/Queue';

type SuccessResponse = {
  estimatedQuota: number;
};

export const getEstimatedQuota = async ({
  objectId,
  objectType,
}: {
  objectId: string;
  objectType: ActionType;
}): Promise<WordpressAnswer<SuccessResponse>> => {
  const action = 'traduire-sans-migraine_get_object_estimated_quota';
  const response = await axiosClient.get<SuccessResponse>('', {
    params: {
      action,
      objectType,
      objectId,
    },
  });

  return response.data;
};
