import axiosClient from '../axiosClient';
import { WordpressAnswer } from '../typesResponse';

type SuccessResponse = {
  translating: number;
};

export const translateBulk = async (
  languageSlug: string,
  objectType:
    | 'pages'
    | 'categories'
    | 'attributes'
    | 'products'
    | 'tags'
    | 'emails'
): Promise<WordpressAnswer<SuccessResponse>> => {
  const action = 'traduire-sans-migraine_woocommerce_translate_bulk';
  const response = await axiosClient.post<SuccessResponse>(
    '',
    {
      languageSlug,
      objectType,
    },
    {
      params: {
        action,
      },
    }
  );

  return response.data;
};
