import axiosClient from '../axiosClient';
import { WordpressAnswer } from '../typesResponse';

type SuccessResponse = Array<{
  label: string;
  id: string;
  translations: Record<string, { label: string; id: string }>;
}>;

export const getPostsUnlinked = async ({
  search,
  languageId,
  currentId,
  currentLanguageId,
}: {
  search: string;
  languageId: string | number;
  currentId?: string | number;
  currentLanguageId: string | number;
}): Promise<WordpressAnswer<SuccessResponse>> => {
  const action = 'tsm_wc_get_products_not_linked';
  const response = await axiosClient.get<SuccessResponse>('', {
    params: {
      action,
      term: search,
      languageId,
      currentId,
      block: true,
      currentLanguageId,
    },
  });

  return response.data;
};
