import axiosClient from '../axiosClient';
import { WordpressAnswer } from '../typesResponse';
import { Language } from '../../types/Language';

type SuccessResponse = {
  languages: Array<Language>;
};

export const getLanguages = async (): Promise<
  WordpressAnswer<SuccessResponse>
> => {
  const action = 'traduire-sans-migraine_get_languages';
  const response = await axiosClient.get<SuccessResponse>(``, {
    params: { action },
  });

  return response.data;
};
