import { createAction } from './createAction';
import { ActionType } from '../../types/Queue';

export const addToQueue = async ({
  objectId,
  objectType,
  slugTo,
}: {
  objectId: string;
  objectType: ActionType;
  slugTo: string;
}) =>
  createAction({
    slugTo,
    havePriority: false,
    objectId,
    objectType,
  });
