import axiosClient from '../axiosClient';
import { WordpressAnswer } from '../typesResponse';

type SuccessResponse = Record<string, never>;

export const sendReasonsDeactivate = async (
  reason: string
): Promise<WordpressAnswer<SuccessResponse>> => {
  const action = 'traduire-sans-migraine_send_reasons_deactivate';
  const response = await axiosClient.post<SuccessResponse>(
    '',
    {
      reason,
    },
    {
      params: {
        action,
      },
    }
  );

  return response.data;
};
