import { useTranslation as useTranslationBase } from 'react-i18next';
import { TranslationKeys } from './translationKeys';

export const useTranslation = (domain?: string) => {
  const { t: tBase, ...othersArgs } = useTranslationBase(domain);
  return {
    ...othersArgs,
    t: (x: TranslationKeys, options?: Record<string, unknown>) => {
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      const translation = tBase(x as any, options as any);
      if (
        window.location.host === 'localhost' &&
        translation.startsWith('//@TODO: Add translation')
      ) {
        return `${x}`;
      }
      return translation;
    },
  };
};
