import axiosClient from '../axiosClient';
import { WordpressAnswer } from '../typesResponse';
import { ActionType } from '../../types/Queue';

export type SuccessResponse = {
  ID: string;
};

export const createAction = async ({
  objectId,
  slugTo,
  havePriority,
  objectType,
}: {
  objectId: string | number;
  slugTo: string;
  havePriority: boolean;
  objectType: ActionType;
}): Promise<WordpressAnswer<SuccessResponse>> => {
  const action = 'traduire-sans-migraine_add_to_queue';
  const response = await axiosClient.post<SuccessResponse>(
    '',
    {
      objectId,
      languageTo: slugTo,
      ...(havePriority && { havePriority }),
      objectType,
    },
    {
      params: {
        action,
      },
    }
  );

  return response.data;
};
