import axiosClient from './axiosClient';
import { WordpressAnswer } from './typesResponse';

export type SuccessResponse = {
  cron: {
    lastPostId: number;
    lastExecuteTime: number;
    lastCount: number;
  };
  countFixable: number;
  countAll: number;
  countFixed: number;
  postsCount: number;
  postsToAnalyzed: number;
};
export const getInternalsLinksState = async (): Promise<
  WordpressAnswer<SuccessResponse>
> => {
  const action = 'traduire-sans-migraine_get_internals_links_state';
  const response = await axiosClient.get<SuccessResponse>('', {
    params: {
      action,
    },
  });

  return response.data;
};
