import axiosClient from '../axiosClient';
import { WordpressAnswer } from '../typesResponse';
import { QueueItem } from '../../types/Queue';

type SuccessResponse = {
  queue: Array<QueueItem>;
};

export const getQueue = async (): Promise<WordpressAnswer<SuccessResponse>> => {
  const action = 'traduire-sans-migraine_get_queue';
  const response = await axiosClient.get<SuccessResponse>('', {
    params: {
      action,
    },
  });

  return response.data;
};
