/* eslint-disable import/no-relative-packages */
import { useDispatch } from 'react-redux';
import { useEffect, useState } from '@wordpress/element';
import { Spin } from 'antd';
import * as React from 'react';
import { ClientAPI } from '../../../../shared/API';
import {
  actions as actionsSettings,
  reducer as reducerSettings,
  sliceKey as sliceKeySettings,
} from '../../../../shared/store/settings/slice';
import { useInjectReducer } from '../../../../shared/utils/redux-injectors';
import { keepOnlyLanguagesEnabledUniqueSlug } from '../../../../shared/utils/languages';
import Block from './block';

function Container({
  productId,
  attributes,
  context,
}: {
  productId: string;
  attributes: Record<string, unknown>;
  context: { postType: string };
}) {
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-expect-error
  useInjectReducer({ key: sliceKeySettings, reducer: reducerSettings });
  const dispatch = useDispatch();
  const [loading, setLoading] = useState(true);

  const getLanguages = async () => {
    setLoading(true);
    const response = await ClientAPI.languages.get();
    if (response.success) {
      const languagesEnabled = response.data.languages.reduce(
        keepOnlyLanguagesEnabledUniqueSlug,
        []
      );
      dispatch(actionsSettings.setLanguages(languagesEnabled));
    }
    setLoading(false);
  };

  useEffect(() => {
    getLanguages();
  }, []);

  if (loading) {
    return <Spin spinning />;
  }

  return (
    <Block productId={productId} attributes={attributes} context={context} />
  );
}

export default Container;
