/* eslint-disable import/no-relative-packages */
import { Select, Typography } from 'antd';
import * as React from 'react';
import { useWooBlockProps } from '@woocommerce/block-templates';
import { useEffect, useState } from '@wordpress/element';
import { useSelector } from 'react-redux';
import { ClientAPI } from '../../../../shared/API';
import { useTranslation } from '../../../../shared/i18n/useTranslation';
import { SelectLanguages } from '../../../../shared/components/SelectLanguages';
import { FlagLanguage } from '../../../../shared/components/FlagLanguage';
import { Separator } from '../../../../Settings/LoggedApp/Settings/Account/style';
import { selectSettingsState } from '../../../../shared/store/settings/selectors';
import useStateWoocommerce from './useStateWoocommerce';

function Block({
  productId,
  attributes,
  context,
}: {
  productId: string;
  attributes: Record<string, unknown>;
  context: { postType: string };
}) {
  const { t } = useTranslation();
  const { languages } = useSelector(selectSettingsState);
  const blockProps = useWooBlockProps(attributes);
  const [searching, setSearching] = useState<Record<string, boolean>>({});
  const [search, setSearch] = useState<Record<string, string>>({});
  const [selectedLanguage, setSelectedLanguage] = useStateWoocommerce<
    string | number
  >(context, 'selectedLanguage', languages.find((l) => l.default)!.id);
  const [translations, setTranslations] = useStateWoocommerce<
    Record<
      string,
      {
        label: string;
        id: string | number;
        value: string | number;
      } | null
    >
  >(context, 'translations', {});
  const [originalLanguageId, setOriginalLanguageId] = useState<
    string | number | undefined
  >(0);
  const [options, setOptions] = useState<
    Record<
      string,
      Array<{
        label: string;
        id: string | number;
        value: string | number;
        translations: Record<string, { label: string; id: string | number }>;
      }>
    >
  >({});
  const [selectIsFocus, setSelectIsFocus] = useState({});
  const selectsRef = languages.reduce(
    (acc, language) => ({
      ...acc,
      [language.slug]: React.createRef(),
    }),
    {}
  );

  const makeRequestSearch = async (slug: string, value: string) => {
    if (!selectedLanguage) {
      return;
    }
    setSearching((old) => ({ ...old, [slug]: true }));
    const response = await ClientAPI.posts.getPostsUnlinked({
      search: value,
      languageId: languages.find((language) => language.slug === slug)!.id,
      currentId: productId,
      currentLanguageId: selectedLanguage,
    });
    if (response.success) {
      setOptions((old) => ({
        ...old,
        [slug]: response.data.map((item) => ({
          label: item.label,
          id: item.id,
          value: item.id,
          translations: item.translations,
        })),
      }));
    }
    setSearching((old) => ({ ...old, [slug]: false }));
  };

  const onSearchStart = (slug: string, value: string) => {
    setSearch((old) => ({ ...old, [slug]: value }));
    if (!value.length) {
      return;
    }
    setOptions((old) => ({
      ...old,
      [slug]: [],
    }));
    setTranslations({
      ...translations,
      [slug]: null,
    });
    makeRequestSearch(slug, value);
  };

  useEffect(() => {
    setOriginalLanguageId(selectedLanguage);
    if (translations) {
      setOptions(
        Object.keys(translations).reduce(
          (acc: typeof options, slug) => ({
            ...acc,
            [slug]: translations[slug]
              ? [
                  {
                    label: translations[slug]!.label,
                    id: translations[slug]!.id,
                    value: translations[slug]!.id,
                    translations: {},
                  },
                ]
              : [],
          }),
          {}
        )
      );
    }
  }, []);

  return (
    <div {...blockProps}>
      <Typography.Title level={4}>
        {t('blocks.woocommerce_languages.selectLanguage.title')}
      </Typography.Title>
      <SelectLanguages
        style={{ width: '100%' }}
        value={languages.find((l) => +l.id === +selectedLanguage)?.locale}
        onChange={(locale) => {
          const newLanguageSelected = languages.find(
            (l) => l.locale === locale
          )!;
          setSelectedLanguage(newLanguageSelected.id);
        }}
        include={languages
          .filter(
            (language) =>
              (translations && !translations[language.slug]) ||
              language.id === originalLanguageId
          )
          .map((language) => language.locale)}
      />
      <Separator />
      <Typography.Title level={4}>
        {t('blocks.woocommerce_languages.translations.title')}
      </Typography.Title>
      {languages
        .filter(
          (language) =>
            language.id !== selectedLanguage && language.slug in translations
        )
        .map((language) => (
          <div key={language.locale} style={{ marginTop: '1rem' }}>
            <Select
              ref={selectsRef[language.slug]}
              allowClear
              onClear={() => {
                setTranslations({ ...translations, [language.slug]: null });
              }}
              style={{ width: '100%' }}
              suffixIcon={<FlagLanguage language={language} />}
              placeholder="Search for a product"
              filterOption={false}
              value={translations[language.slug]}
              loading={searching[language.slug]}
              options={options[language.slug]}
              searchValue={search[language.slug]}
              onFocus={() =>
                setSelectIsFocus((old) => ({ ...old, [language.slug]: true }))
              }
              onBlur={() =>
                setSelectIsFocus((old) => ({ ...old, [language.slug]: false }))
              }
              showSearch={selectIsFocus[language.slug]}
              onSearch={(value) => onSearchStart(language.slug, value)}
              onSelect={(value) => {
                const selectedOption = options[language.slug].find(
                  (item) => `${item.id}` === `${value}`
                )!;
                const forceTranslations = Object.keys(
                  selectedOption.translations || {}
                ).reduce(
                  (acc, key) => ({
                    ...acc,
                    [key]: {
                      value: selectedOption.translations[key].id,
                      label: selectedOption.translations[key].label,
                      id: selectedOption.translations[key].id,
                    },
                  }),
                  {}
                );
                const newTranslations = {
                  ...translations,
                  ...forceTranslations,
                  [language.slug]: {
                    value: selectedOption.value,
                    id: selectedOption.id,
                    label: selectedOption.label,
                  },
                };
                setTranslations(newTranslations);
                if (selectsRef[language.slug].current) {
                  selectsRef[language.slug].current.blur();
                }
              }}
            />
            {translations && translations[language.slug] ? (
              <a
                href={`/wp-admin/post.php?post=${
                  translations[language.slug]!.id
                }&action=edit`}
                target="_blank"
                rel="noreferrer"
              >
                {t('blocks.woocommerce_languages.edit')}
              </a>
            ) : (
              ''
            )}
          </div>
        ))}
      <Separator />
    </div>
  );
}

export default Block;
