import { Select, Space, Typography } from 'antd';
import * as React from 'react';
import { CSSProperties } from 'react';
import { useSelector } from 'react-redux';
import { getFlagsDirectory } from '../../API/getTraduireSansMigraineVariables';
import { selectSettingsState } from '../../store/settings/selectors';
import { FlagLanguage } from '../FlagLanguage';
import { useTranslation } from '../../i18n/useTranslation';

export function SelectLanguages({
  exclude = [],
  include = [],
  style = {},
  value,
  onChange,
  uniqueSlug = true,
  loading = false,
  disabled = false,
  multiple = false,
}: {
  exclude?: Array<string>;
  include?: Array<string>;
  style?: CSSProperties;
  uniqueSlug?: boolean;
  loading?: boolean;
  disabled?: boolean;
} & (
  | {
      value: string | undefined;
      onChange: (newValue: string | undefined) => void;
      multiple?: false;
    }
  | {
      value: Array<string>;
      onChange: (newValue: Array<string>) => void;
      multiple: true;
    }
)) {
  const { languages } = useSelector(selectSettingsState);
  const { t } = useTranslation();
  const languagesOptions = languages.reduce((options, language) => {
    if (
      uniqueSlug &&
      options.some((option) => option.label === language.simple_name)
    ) {
      return options;
    }
    const languagesWithSameSlugs = languages.filter(
      (l) => l.slug === language.slug
    );
    if (
      (exclude || []).includes(language.locale) ||
      (uniqueSlug &&
        (exclude || []).some((excludedLocale) =>
          languagesWithSameSlugs.some((l) => l.locale === excludedLocale)
        ))
    ) {
      return options;
    }
    if (
      (include || []).length > 0 &&
      !(include || []).includes(language.locale)
    ) {
      return options;
    }

    return [
      ...options,
      {
        label: uniqueSlug ? language.simple_name : language.name,
        value: language.locale,
        flag: language.flag,
      },
    ];
  }, []);

  languagesOptions.sort((langA, langB) => {
    if (!langA.value) {
      return 1;
    }
    if (!langB.value) {
      return 1;
    }
    return langA.label.localeCompare(langB.label);
  });

  const optionRender = (option: {
    data: (typeof languagesOptions)[number];
  }) => (
    <Space>
      {option.data.flag && (
        <img
          src={`${getFlagsDirectory()}/${option.data.flag}.png`}
          alt={option.data.flag}
        />
      )}
      {option.data.label}
    </Space>
  );

  const labelRender = ({
    value: valueLabelRender,
  }: {
    value: string;
    label: string;
  }) => {
    const option = languagesOptions.find(
      (language) => language.value === valueLabelRender
    );
    if (!option) {
      return <Space />;
    }

    return (
      <Space>
        {option.flag && <FlagLanguage language={option} />}
        {option.label}
      </Space>
    );
  };

  return (
    <Select
      mode={multiple ? 'multiple' : undefined}
      style={{ textAlign: 'center', ...style }}
      options={languagesOptions}
      value={value}
      onChange={onChange}
      optionRender={optionRender}
      labelRender={labelRender}
      loading={loading}
      disabled={disabled}
      placeholder={
        <Typography.Text color="black">
          {t('shared.components.SelectLanguages.placeholder')}
        </Typography.Text>
      }
      status={
        multiple && value !== undefined && value.length === 0
          ? 'error'
          : undefined
      }
    />
  );
}
