import axiosClient from '../axiosClient';
import { WordpressAnswer } from '../typesResponse';

export type SuccessResponse = Record<
  'pages' | 'categories' | 'attributes' | 'products' | 'tags' | 'emails',
  {
    missing: number;
    estimatedQuota: number;
    translating: number;
  }
>;

export const getState = async (
  languageSlug: string
): Promise<WordpressAnswer<SuccessResponse>> => {
  const action = 'traduire-sans-migraine_woocommerce_get_state';
  const response = await axiosClient.get<SuccessResponse>('', {
    params: {
      action,
      languageSlug,
    },
  });

  return response.data;
};
