import axiosClient from '../axiosClient';
import { WordpressAnswer } from '../typesResponse';
import { ActionType } from '../../types/Queue';

export type SuccessResponse = {
  objectsType: Array<
    Omit<ActionType, 'POST_PAGE_PRODUCT'> | 'POST_PAGE' | 'PRODUCT'
  >;
};

export const getObjectsType = async (): Promise<
  WordpressAnswer<SuccessResponse>
> => {
  const action = 'traduire-sans-migraine_get_objects_type';
  const response = await axiosClient.get<SuccessResponse>('', {
    params: {
      action,
    },
  });

  return response.data;
};
