const handleButton = () => {
  const button = document.querySelector(
    '#display-traduire-sans-migraine-button'
  );
  if (window && 'wp' in window && window.wp.data && button) {
    const moveButtonToHeader = () => {
      const headerElement = document.querySelector(
        '.woocommerce-product-header__actions'
      );

      if (headerElement) {
        headerElement.prepend(button);
      } else {
        setTimeout(moveButtonToHeader, 100);
      }
    };
    moveButtonToHeader();
  }
};

export default handleButton;
