import { Language } from '../types/Language';

function reducer(rules: Record<'uniqueSlug' | 'enabledOnly', boolean>) {
  return (acc: Array<Language>, l) => {
    if (rules.enabledOnly && !l.enabled) {
      return acc;
    }
    if (rules.uniqueSlug && acc.some((al) => al.slug === l.slug)) {
      return acc;
    }
    if (l.options.country && l.options.country !== l.locale) {
      return acc;
    }
    return [...acc, l];
  };
}

export const keepOnlyLanguagesUniqueSlug = reducer({ uniqueSlug: true, enabledOnly: false });

export const keepOnlyLanguagesEnabledUniqueSlug = reducer({ uniqueSlug: true, enabledOnly: true });