import { UseStateWooCommerce } from './type';

function useStateWoocommerce<T>(
  context: Record<string, unknown>,
  name: string,
  defaultValue: T
) {
  if (!('wc' in window)) {
    throw new Error('WooCommerce is not loaded');
  }
  return window.wc.productEditor.__experimentalUseProductEntityProp(name, {
    ...context,
    fallbackValue: defaultValue,
  }) as UseStateWooCommerce<T>;
}

export default useStateWoocommerce;
