import axiosClient from '../axiosClient';
import { WordpressAnswer } from '../typesResponse';

type SuccessResponse = {
  message: string;
  title: string;
  logo: string;
};

type BodyRequest = {
  language: string;
};

export const addNewLanguage = async ({
  body,
}: {
  body: BodyRequest;
}): Promise<WordpressAnswer<SuccessResponse>> => {
  const action = 'traduire-sans-migraine_add_new_language';
  const response = await axiosClient.post<SuccessResponse>(``, body, {
    params: { action },
  });

  return response.data;
};
