import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import { translations } from './translations';

export const i18nInit = (lng: string) => {
  i18n.use(initReactI18next).init({
    supportedLngs: ['en', 'fr'],
    resources: translations,
    fallbackLng: 'en',
    lng,
    interpolation: {
      escapeValue: false,
    },
  });
};
