import axios from 'axios';
import { getAjaxUrl, getWpNonce } from './getTraduireSansMigraineVariables';

const axiosClient = axios.create({
  headers: {
    'Content-Type': 'application/json',
  },
});

axiosClient.interceptors.request.use((config) => {
  const wpNonce = getWpNonce();
  const newConfig = { ...config };
  newConfig.baseURL = getAjaxUrl();
  if (newConfig.data) {
    newConfig.data.wpNonce = wpNonce;
  } else if (newConfig.params) {
    newConfig.params.wpNonce = wpNonce;
  }
  return newConfig;
});

// avoid catch errors
axiosClient.interceptors.response.use(
  (response) => response,
  (error) => Promise.resolve(error.response)
);

export default axiosClient;
