import axiosClient from '../axiosClient';
import { WordpressAnswer } from '../typesResponse';

type SuccessResponse = {
  message: string;
  title: string;
  logo: string;
};

type BodyRequest = {
  slug: string;
} & Partial<{
  country: string;
  formality: 'less' | 'more' | 'default';
}>;

export const updateOptions = async ({
  body,
}: {
  body: BodyRequest;
}): Promise<WordpressAnswer<SuccessResponse>> => {
  const action = 'traduire-sans-migraine_update_language_settings';
  const response = await axiosClient.post<SuccessResponse>(``, body, {
    params: { action },
  });

  return response.data;
};
