import * as React from 'react';
import { Tooltip } from 'antd';
import { getFlagsDirectory } from '../../API/getTraduireSansMigraineVariables';
import { Language } from '../../types/Language';

export function FlagLanguage(
  props:
    | {
        language: Language | { flag: string };
        tooltip: false;
      }
    | {
        language: Language | { flag: string; name: string };
        tooltip: true;
      }
) {
  // eslint-disable-next-line react/destructuring-assignment
  if (props.tooltip) {
    const { language } = props;
    return (
      <Tooltip title={language.name}>
        <img
          src={`${getFlagsDirectory()}/${language.flag}.png`}
          alt={language.name}
        />
      </Tooltip>
    );
  }
  const { language } = props;
  return (
    <img
      src={`${getFlagsDirectory()}/${language.flag}.png`}
      alt={language.flag}
    />
  );
}
