import axiosClient from '../axiosClient';
import { WordpressAnswer } from '../typesResponse';
import { ActionType } from '../../types/Queue';

type SuccessResponse = Record<string, never>;

export const setLanguage = async ({
  objectId,
  objectType,
  slug,
}: {
  objectId: string;
  objectType: ActionType;
  slug: string;
}): Promise<WordpressAnswer<SuccessResponse>> => {
  const action = 'traduire-sans-migraine_set_language';
  const response = await axiosClient.put<SuccessResponse>(
    '',
    {
      objectId,
      objectType,
      slug,
    },
    {
      params: {
        action,
      },
    }
  );

  return response.data;
};
