import axiosClient from '../axiosClient';
import { WordpressAnswer } from '../typesResponse';
import { Account } from '../../types/Account';

type SuccessResponse =
  | {
      account: Account;
      redirect: null;
      token: string;
    }
  | {
      account: null;
      token: null;
      redirect: {
        url: string;
      };
    };
export const getAccount = async (): Promise<
  WordpressAnswer<SuccessResponse>
> => {
  const action = 'traduire-sans-migraine_get_account';
  const response = await axiosClient.get<SuccessResponse>('', {
    params: { action },
  });

  return response.data;
};
