import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { ContainerState } from './types';

export const initialState: ContainerState = {
  activeTab: localStorage.getItem('settingsTab') || '1',
  languages: [],
};

const settingsSlice = createSlice({
  name: 'settingsState',
  initialState,
  reducers: {
    setActiveTab(state, payload: PayloadAction<ContainerState['activeTab']>) {
      state.activeTab = payload.payload;
      localStorage.setItem('settingsTab', payload.payload);
    },
    setLanguages(state, payload: PayloadAction<ContainerState['languages']>) {
      state.languages = payload.payload;
    },
    updateLanguage(
      state,
      payload: PayloadAction<
        Partial<ContainerState['languages'][number]> & { slug: string }
      >
    ) {
      state.languages = state.languages.map((language) => {
        if (language.slug !== payload.payload.slug) {
          return language;
        }
        return {
          ...language,
          ...payload.payload,
        };
      });
    },
  },
});

export const { actions, reducer, name: sliceKey } = settingsSlice;
