import axiosClient from './axiosClient';
import { WordpressAnswer } from './typesResponse';
import { TraduireSansMigraineVariablesResponse } from '../types/traduireSansMigraineVariables';
import { ActionType } from '../types/Queue';

type SuccessResponse = TraduireSansMigraineVariablesResponse;
export const getVariables = async ({
  objectId,
  objectType,
}: {
  objectId: string;
  objectType: ActionType;
}): Promise<WordpressAnswer<SuccessResponse>> => {
  const action = 'traduire-sans-migraine_get_variables';
  const response = await axiosClient.get<SuccessResponse>('', {
    params: {
      action,
      objectId,
      objectType,
    },
  });

  return response.data;
};
