import {
  TraduireSansMigraineVariables,
  TraduireSansMigraineVariablesResponse,
} from '../types/traduireSansMigraineVariables';

export const injectVariables = (
  variables: Partial<TraduireSansMigraineVariables>
) => {
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-expect-error
  window.traduireSansMigraineVariables =
    window.traduireSansMigraineVariables || {};

  if ('traduireSansMigraineVariables' in window) {
    window.traduireSansMigraineVariables = {
      ...window.traduireSansMigraineVariables,
      ...variables,
    };
  }
};

export const setVariablesResponse = (
  variables: TraduireSansMigraineVariablesResponse
) => {
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-expect-error
  window.traduireSansMigraineVariablesResponse = variables;
};
const getTraduireSansMigraineVariables = (): TraduireSansMigraineVariables =>
  'traduireSansMigraineVariables' in window
    ? (window.traduireSansMigraineVariables as TraduireSansMigraineVariables)
    : {
        ajaxUrl: '',
        objectId: '',
        currentLocale: '',
        nonce: '',
        objectType: 'POST_PAGE_PRODUCT',
        polylangUrl: '',
        token: '',
        urlClient: '',
      };

const getTraduireSansMigraineVariablesResponse =
  (): TraduireSansMigraineVariablesResponse =>
    'traduireSansMigraineVariablesResponse' in window
      ? (window.traduireSansMigraineVariablesResponse as TraduireSansMigraineVariablesResponse)
      : {
          objectType: 'POST_PAGE_PRODUCT',
        };

export const getAjaxUrl = () => {
  const variables = getTraduireSansMigraineVariables();
  return variables.ajaxUrl;
};

export const getToken = () => {
  const variables = getTraduireSansMigraineVariables();
  return variables.token;
};

export const getWpNonce = () => {
  const variables = getTraduireSansMigraineVariables();
  return variables.nonce;
};

export const getCurrentLocale = () => {
  const variables = getTraduireSansMigraineVariables();
  return variables.currentLocale;
};

export const getFlagsDirectory = () => {
  const variables = getTraduireSansMigraineVariables();
  return `${variables.polylangUrl}/flags`;
};

export const getUrlClient = () => {
  const variables = getTraduireSansMigraineVariables();
  return variables.urlClient;
};

export const getObjectId = () => {
  const variables = getTraduireSansMigraineVariables();
  return variables.objectId;
};

export const getObjectType = () => {
  const variables = getTraduireSansMigraineVariables();
  return variables.objectType;
};

export const getTranslations =
  (): TraduireSansMigraineVariablesResponse['translations'] => {
    const variables = getTraduireSansMigraineVariablesResponse();
    return variables.translations;
  };

export const getFirstVisitAfterTSMTranslatedIt =
  (): TraduireSansMigraineVariablesResponse['firstVisitAfterTSMTranslatedIt'] => {
    const variables = getTraduireSansMigraineVariablesResponse();
    return Boolean(variables.firstVisitAfterTSMTranslatedIt);
  };

export const getHasTSMTranslatedIt =
  (): TraduireSansMigraineVariablesResponse['hasTSMTranslatedIt'] => {
    const variables = getTraduireSansMigraineVariablesResponse();
    return Boolean(
      variables.objectType === 'POST_PAGE_PRODUCT' &&
        variables.hasTSMTranslatedIt
    );
  };

export const getTranslatedFromSlug =
  (): TraduireSansMigraineVariablesResponse['translatedFromSlug'] => {
    const variables = getTraduireSansMigraineVariablesResponse();
    return variables.objectType === 'POST_PAGE_PRODUCT'
      ? variables.translatedFromSlug
      : null;
  };

export const getSummary =
  (): TraduireSansMigraineVariablesResponse['summary'] => {
    const variables = getTraduireSansMigraineVariablesResponse();
    return variables.objectType === 'POST_PAGE_PRODUCT'
      ? variables.summary
      : null;
  };
