import axiosClient from '../axiosClient';
import { WordpressAnswer } from '../typesResponse';
import { Post } from '../../types/Post';
import { ActionType } from '../../types/Queue';

export type Email = {
  emailId: string;
  title: string;
  enabled: 'yes' | 'no';
  translationMap: Record<
    string,
    {
      translationExists: boolean;
      translationIsUpdated: boolean;
    }
  >;
  filters: {
    display: boolean;
  };
  estimatedQuota: number | null | string;
} & Record<
  string,
  { subject: string; heading: string; additional_content: string }
>;

export type Terms = {
  ID: string;
  name: string;
  slug: string;
  description: string;
  taxonomy: string;
  translationMap: Record<
    string,
    {
      translationExists: boolean;
      translationIsUpdated: boolean;
    }
  >;
  filters: {
    display: boolean;
  };
  estimatedQuota: number | null | string;
};

export type SuccessResponse = {
  objects: Array<Post | Email | Terms>;
  pagination: {
    total: number;
    pageSize: number;
    current: number;
  };
};

export type Params = {
  slugSourceLanguage: string;
  sortField?: null | string;
  sortOrder?: null | 'ascend' | 'descend';
  filters?: Record<string, null | Array<string>>;
  pageSize?: number;
  page?: number;
  objectType: Omit<ActionType, 'POST_PAGE_PRODUCT'> | 'POST_PAGE' | 'PRODUCT';
};
export const getObjects = async ({
  slugSourceLanguage,
  sortField,
  sortOrder,
  filters,
  pageSize,
  page,
  objectType,
}: Params): Promise<WordpressAnswer<SuccessResponse>> => {
  const action = 'traduire-sans-migraine_get_objects';
  const response = await axiosClient.post<SuccessResponse>(
    '',
    {
      from: slugSourceLanguage,
      filters,
      objectType,
    },
    {
      params: {
        action,
        sortField,
        sortOrder,
        pageSize,
        page,
      },
    }
  );

  return response.data;
};
