import axiosClient from '../axiosClient';
import { WordpressAnswer } from '../typesResponse';
import { ActionType, QueueItem } from '../../types/Queue';

type SuccessResponse = {
  actions: Array<QueueItem>;
  actionPaused: QueueItem | null;
};

export const getActionsByObject = async (
  objectId: string,
  objectType: ActionType
): Promise<WordpressAnswer<SuccessResponse>> => {
  const action = 'traduire-sans-migraine_get_actions_by_object';
  const response = await axiosClient.get<SuccessResponse>('', {
    params: {
      action,
      objectType,
      objectId,
    },
  });

  return response.data;
};
